<template>
  <div class="container">
    <el-button style="margin-left: 8px;" @click="$router.push('/business/create')" type="success"
      >新建商务合作</el-button
    >
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="tel" label="电话" align="center" />
      <el-table-column prop="email" label="邮箱" align="center" />
      <el-table-column prop="thumb" width="400" label="图片" align="center">
        <template slot-scope="scope">
          <img class="img" :src="scope.row.thumb" alt="" />
        </template>
      </el-table-column>
      <el-table-column prop="status_name" label="是否开启" align="center" />
      <el-table-column prop="address" align="center" width="220" label="操作">
        <template slot-scope="scope">
          <el-button @click="editItem(scope.row)" type="primary" size="small">编辑</el-button>
          <el-button @click="removeItem(scope.row)" type="danger" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'
export default {
  data() {
    return {
      tableData: [],
      count: 0,
      page_curren: 1,
      loading: false,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/article/businesscoo/search')
        .then(r => {
          this.loading = false
          this.tableData = r.data.list
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    editItem(row) {
      this.$router.push('/business/edit/' + row.id)
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/article/businesscoo/del', { id: row.id })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('删除成功')
                done()
                this.FetchList()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('删除失败')
              })
          } else {
            done()
          }
        },
      })
    },
  },
}
</script>
